'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { format } from 'date-fns'
import { CalendarIcon, Info, Loader2, Plus } from 'lucide-react'

import { Button } from '@/components/ui/button'
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from '@/components/ui/card'
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { Calendar } from '@/components/ui/calendar'
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '@/components/ui/popover'
import { Switch } from '@/components/ui/switch'
import { Textarea } from '@/components/ui/textarea'
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'
import { cn } from '@/lib/utils'

import axiosClient from '@/lib/axiosClient'
import { showToast } from '@/lib/showToast'
export default function BulkImportContactsPage() {
  const router = useRouter()
  const [file, setFile] = useState<File | null>(null)
  const [parsedRows, setParsedRows] = useState<any[]>([])
  const [mapping, setMapping] = useState<Record<string, string>>({})
  const [importSource, setImportSource] = useState('csv_import')
  const [deduplicate, setDeduplicate] = useState(true)
  const [isImporting, setIsImporting] = useState(false)

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selected = e.target.files?.[0]
    if (!selected?.name.endsWith('.csv')) {
      showToast('Please upload a .csv file', 'error')
      return
    }
    setFile(selected)

    Papa.parse(selected, {
      header: true,
      skipEmptyLines: true,
      complete: (result) => {
        setParsedRows(result.data.slice(0, 10)) // preview first 10
        // auto-suggest mapping based on headers
        const headers = result.meta.fields || []
        const initialMapping: Record<string, string> = {}
        headers.forEach(h => {
          const lower = h.toLowerCase()
          if (lower.includes('first') || lower.includes('fname')) initialMapping[h] = 'first_name'
          if (lower.includes('last')  || lower.includes('lname')) initialMapping[h] = 'last_name'
          if (lower.includes('email')) initialMapping[h] = 'email'
          if (lower.includes('phone')) initialMapping[h] = 'phone'
          if (lower.includes('company')) initialMapping[h] = 'company'
        })
        setMapping(initialMapping)
      }
    })
  }

  const handleImport = async () => {
    if (!file || parsedRows.length === 0) return

    setIsImporting(true)

    // In real app: send file to backend or parse & send batch
    // Here we show example sending one-by-one (not ideal — prefer bulk endpoint)

    try {
      // Option A: If backend supports bulk → send array
      // const contacts = parsedRows.map(row => mapToContact(row, mapping, importSource))
      // await axiosClient.post('/contacts/bulk', { contacts, deduplicate })

      // Option B: loop (safer for now)
      let successCount = 0
      for (const row of parsedRows) {
        const contact = {
          first_name: row[mapping.first_name] || '',
          last_name: row[mapping.last_name] || '',
          email: row[mapping.email]?.trim(),
          phone: row[mapping.phone]?.trim(),
          company: row[mapping.company]?.trim(),
          contact_type: 'manual_bulk_import',
          contact_source: importSource,
          deduplicate,
          // source_metadata: { imported_from: file.name, imported_at: new Date().toISOString() }
        }

        try {
          await axiosClient.post('/contacts', contact)
          successCount++
        } catch (err: any) {
          if (err.response?.status === 409) {
            // skipped duplicate
          } else {
            console.warn('Import row failed:', row, err)
          }
        }
      }

      showToast(`Imported ${successCount} contacts successfully`, 'success')
      router.push('/dashboard/contacts')

    } catch (err) {
      showToast('Bulk import failed', 'error')
    } finally {
      setIsImporting(false)
    }
  }

  return (
    <div className="container py-10">
      <Card>
        <CardHeader>
          <CardTitle>Import Contacts</CardTitle>
          <CardDescription>
            Upload CSV file • Bulk creation with origin tracking
          </CardDescription>
        </CardHeader>

        <CardContent className="space-y-6">
          {/* File upload */}
          <div>
            <label className="block text-sm font-medium">Upload CSV</label>
            <Input type="file" accept=".csv" onChange={handleFileChange} />
            <p className="text-xs text-muted-foreground mt-1">
              Expected columns: first_name, last_name, email, phone, company, ...
            </p>
          </div>

          {/* Source selection */}
          <div>
            <label className="block text-sm font-medium">Import Source</label>
            <Select value={importSource} onValueChange={setImportSource}>
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="csv_import">CSV Import (via dashboard)</SelectItem>
                <SelectItem value="zapier">Zapier Integration</SelectItem>
                <SelectItem value="api">API / External System</SelectItem>
                {/* Add more if you expose them */}
              </SelectContent>
            </Select>
            <p className="text-xs text-muted-foreground mt-1">
              Will be stored as contact_source (immutable)
            </p>
          </div>

          {/* Deduplicate toggle */}
          <div className="flex items-center space-x-2">
            <Switch id="dedup" checked={deduplicate} onCheckedChange={setDeduplicate} />
            <label htmlFor="dedup" className="text-sm font-medium">
              Skip duplicates (email/phone)
            </label>
          </div>

          {/* Preview + mapping UI would go here */}

          <Button onClick={handleImport} disabled={!file || isImporting}>
            {isImporting ? 'Importing...' : 'Start Import'}
          </Button>
        </CardContent>
      </Card>
    </div>
  )
}